<template>
  <div ref="multiCube"></div>
</template>

<script>
const THREEUTIL = require('threejs-utils')
export default {
  name: 'add-multi-cube',
  metaInfo: {
    title: '多个立方体',
    meta: [
      {charset: 'utf-8'},
      {name: 'keywords', content: '很多立方体'},
      {name: 'description', content: '很多立方体'},
      {name: 'viewport', content: 'width=device-width,initial-scale=1,minimum-scale=1,maximum-scale=1,user-scalable=no'}
    ]
  },
  data () {
    return {
      scene: null, // 场景
      geometry: null, // 球体几何对象
      material: null, // 材质对象
      mesh: null, // 网格模型对象
      point: null, // 点光源
      ambient: null, // 环境光
      camera: null, // 相机
      renderer: null, // 渲染器
      controls: null, // 控件
      firstTime: new Date(), // 初始时间
      layout: {}
    }
  },
  created () {
    this.init()
  },
  methods: {
    init: function () {
      /**
       * 创建场景对象 Scene
       */
      this.scene = new THREEUTIL.THREE.Scene()
      /**
       * 创建网格模型
       */
      // 创建一个立方体几何对象Geometry
      this.geometry = new THREEUTIL.THREE.BoxGeometry(100, 100, 100)
      // 创建材质对象 Material
      this.material = new THREEUTIL.THREE.MeshLambertMaterial({
        color: 0x0000ff
      })
      // 创建网格模型对象 Mesh
      this.mesh = new THREEUTIL.THREE.Mesh(this.geometry, this.material)
      // 网格模型添加到场景中
      this.scene.add(this.mesh)
      // 球体网格模型
      let geometry2 = new THREEUTIL.THREE.SphereGeometry(60, 40, 40)
      let material2 = new THREEUTIL.THREE.MeshLambertMaterial({
        color: 0xff00ff
      })
      // 网格模型对象Mesh
      let mesh2 = new THREEUTIL.THREE.Mesh(geometry2, material2)
      // 球体网格模型沿Y轴正方向平移 120
      mesh2.translateY(120)
      this.scene.add(mesh2)
      // 圆柱网格模型
      let geometry3 = new THREEUTIL.THREE.CylinderGeometry(50, 50, 100, 25)
      let material3 = new THREEUTIL.THREE.MeshLambertMaterial({
        color: 0xffff00
      })
      let mesh3 = new THREEUTIL.THREE.Mesh(geometry3, material3)
      mesh3.position.set(120, 0, 0)
      this.scene.add(mesh3)
      /**
       * 光源设置
       */
      // 点光源
      this.point = new THREEUTIL.THREE.PointLight(0xffffff)
      // 点光源设置
      this.point.position.set(400, 200, 300)
      // 将点光源添加到场景中
      this.scene.add(this.point)
      // 环境光
      this.ambient = new THREEUTIL.THREE.AmbientLight(0x444444)
      this.scene.add(this.ambient)
      /**
       * 相机设置
       */
      // 可视窗口宽度
      let width = (this.layout.contentWidth) ? this.layout.contentWidth : document.body.clientWidth || window.innerWidth
      // 可视窗口高度
      let height = (this.layout.contentHeight) ? this.layout.contentHeight : document.body.clientHeight || window.innerHeight
      // console.log(this.layoutVal.sideWidth)
      // 宽高比
      let kgb = width / height
      // 三维场景显示范围控制系数，系数越大，显示范围就越大
      let num = 200
      /**
       * 创建相机对象
       */
      this.camera = new THREEUTIL.THREE.OrthographicCamera(-num * kgb, num * kgb, num, -num, 1, 1000)
      // 设置相机位置
      this.camera.position.set(200, 300, 200)
      // 设置相机的方向(场景对象)
      this.camera.lookAt(this.scene.position)
      /**
       * 创建渲染器
       */
      this.renderer = new THREEUTIL.THREE.WebGLRenderer()
      // 设置渲染区域尺寸
      this.renderer.setSize(width, height)
      // 设置背景颜色
      this.renderer.setClearColor(0xb9d3ff, 1)
      console.log(this.layout.contentHeight)
    },
    render: function () {
      // 执行渲染操作， 指定场景，相机作为参数
      this.renderer.render(this.scene, this.camera)
    }
  },
  mounted () {
    // let _self = this
    // 获取元素对象插入 canvas 对象
    this.$refs.multiCube.appendChild(this.renderer.domElement)
    //
    this.render()
  }
}
</script>

<style scoped>

</style>



